import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import Img from '../images/image_2.jpg'
import Img1 from '../images/image_1.jpg'

const Footer = () => {
    return (
        <Footercom className="ftco-footer">
            <div className="overlay"></div>
            <div className="container">
                <div className="row mb-5">
                    <div className="col-lg-3 col-md-6 mb-5 mb-md-5">
                        <div className="ftco-footer-widget mb-4">
                            <h2 className="ftco-heading-2">About Us</h2>
                            <p>Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts.</p>
                            <ul className="ftco-footer-social list-unstyled float-md-left float-lft mt-5">
                                <li className="ftco-animate fadeInUp ftco-animated"><Link><span className="icon-twitter"><i class="fab fa-twitter"></i></span></Link></li>
                                <li className="ftco-animate fadeInUp ftco-animated"><Link><span className="icon-facebook"><i class="fab fa-facebook-f"></i></span></Link></li>
                                <li className="ftco-animate fadeInUp ftco-animated"><Link><span className="icon-instagram"><i class="fab fa-instagram"></i></span></Link></li>
                            </ul>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-6 mb-5 mb-md-5">
                        <div className="ftco-footer-widget mb-4">
                            <h2 className="ftco-heading-2">Recent Blog</h2>
                            <div className="block-21 mb-4 d-flex justify-content-center">
                                <img src={Img1} className="blog-img mr-4 img-fluid" alt="photo" />
                                <div className="text ms-3">
                                    <h3 className="heading"><Link>Even the all-powerful Pointing has no control about</Link></h3>
                                    <div className="meta">
                                        <div><Link><span className="icon-calendar"></span> Sept 15, 2018</Link></div>
                                        <div><Link><span className="icon-person"></span> Admin</Link></div>
                                        <div><Link><span className="icon-chat"></span> 19</Link></div>
                                    </div>
                                </div>
                            </div>
                            <div className="block-21 mb-4 d-flex">
                                <img className="blog-img mr-4" src={Img} alt="photo" />
                                <div className="text ms-3">
                                    <h3 className="heading"><Link>Even the all-powerful Pointing has no control about</Link></h3>
                                    <div className="meta">
                                        <div><Link><span className="icon-calendar"></span> Sept 15, 2018</Link></div>
                                        <div><Link><span className="icon-person"></span> Admin</Link></div>
                                        <div><Link><span className="icon-chat"></span> 19</Link></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-2 col-md-6 mb-5 mb-md-5">
                        <div className="ftco-footer-widget mb-4 ml-md-4">
                            <h2 className="ftco-heading-2">Services</h2>
                            <ul className="list-unstyled">
                                <li><Link className="py-2 d-block">Cooked</Link></li>
                                <li><Link className="py-2 d-block">Deliver</Link></li>
                                <li><Link className="py-2 d-block">Quality Foods</Link></li>
                                <li><Link className="py-2 d-block">Mixed</Link></li>
                            </ul>
                        </div>
                    </div>
                    <div className="col-lg-3 col-md-6">
                        <div className="ftco-footer-widget mb-4">
                            <h2 className="ftco-heading-2">Have a Questions?</h2>
                            <div className="block-23 mb-3">
                                <ul>
                                    <li><span className="icon icon-map-marker">
                                        <i className="fas fa-map-marker-alt"></i>
                                        </span>
                                        <span className="text">203 Fake St. Mountain View, San Francisco, California, USA</span>
                                    </li>
                                    <li><Link>
                                        <span className="icon icon-phone">
                                            <i className="fal fa-phone-alt"></i>
                                        </span>
                                        <span className="text">+2 392 3929 210</span>
                                        </Link>
                                    </li>
                                    <li><Link>
                                         <span className="icon icon-envelope">
                                             <i className="fal fa-envelope"></i></span>
                                             <span className="text">info@yourdomain.com</span>
                                             </Link>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12 text-center">
                        <p>Copyright ©2021 All rights reserved ❤ This template is made with  by Colorlib</p>
                    </div>
                </div>
            </div>
        </Footercom>
    )
}

const Footercom = styled.div`
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    background: var(--main-bg);
    .container-lg{
        padding: 5rem 0;
    }
    .block-23 ul li .icon {
    width: 40px;
    font-size: 18px;
    padding-top: 2px;
    color: white;
}
.ftco-footer-social li a {
    height: 50px;
    width: 50px;
    display: block;
    float: left;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 50%;
}
.block-23 ul li .icon, .block-23 ul li .text {
    display: table-cell;
    vertical-align: top;
}
h2 {
    display: block;
    font-size: 1.5em;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
    color: white;
}
ul li{
    list-style: none;
    font-size: 18px;
}
  .hero-wrap, .img, .blog-img, .user-img {
    width: 100px;
    height: 90px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
  }
.mb-4, .my-4 {
    margin-bottom: 1.5rem !important;
}
.ftco-footer {
    font-size: 16px;
    padding: 7em 0;
    text-decoration: none;
}
.text{
    text-decoration: none;
    font-size: 16px;
}
.heading a{
    font-size: 16px;
    text-decoration: none;
}
.heading{
    line-height: 1.4rem;
    font-weight: 400;
    font-family: "Josefin Sans", Arial, sans-serif;
}
a{
    text-decoration: none;
    color: white;
}
.ftco-footer p {
    color: white;
}
.overlay{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: #000;
    opacity: 0.6;
    z-index: -1;
}
`
export default Footer
