const Main = styled.div`
    padding: 7em 0;
    .text{
        padding-bottom: 1rem !important;
    }
.heading{
    text-align: center;
    font-size: 30px;
    font-weight: 400;
    color: black;
    line-height: 1.4;
    font-family: "Josefin Sans", Arial, sans-serif;
    color: #fff;
    margin: 0 0 24px;
}
p{
    display: block; 
    text-align: center;
    font-family: "Poppins", Arial, sans-serif;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    margin: 0 0 16px;
    color: gray;
    margin-block-start: 1em;
    margin-block-end: 1em;
}
`
const Menu = styled.div`
    h3{
        text-align: start;
        line-height: 1.4;
        color: #fff;
        font-weight: 400;
        font-family: "Josefin Sans", Arial, sans-serif;
    }
    p{
    text-align: start;
    font-family: "Poppins", Arial, sans-serif;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    color: gray;
    margin-block-start: 1em;
    margin-block-end: 1em;
}
.services-wrap .img, .services-wrap .text {
    width: 50%;
}
.btn{
    font-weight: 400;
    text-align: center;
    /* white-space: nowrap; */
    border: 1px solid white;
    color: white;
    font-size: 1rem;
    line-height: 1.5;
    margin: 0 0 0 14px;
    :hover{
        background: #fac564;
        color: black;
        border: none;
    }
}
.img{
    background: url(${pizza});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
a{
    text-decoration: none;
}
.text-heading{
    background: rgba(0, 0, 0, 0.2);
    width: 50%;
}
.p-4 {
    padding: 1.5rem !important;
}
`
export default MenuCom
