import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import Img1 from '../images/image_1.jpg'
import Img2 from '../images/image_2.jpg'
import Img3 from '../images/image_3.jpg'

const BlogCom = () => {
    return (
        <Section>
            <div className="col-md-12 heading-section text-center">
                <h2 className="heading-text mb-4">Recent from blog</h2>
                <p>Far far away, behind the word mountains, far from the countries Vokalia and<br /> Consonantia, there live the blind texts.</p>
            </div>
            <div className="container-xl">
                <div className="row row-cols-1 row-cols-md-3 g-5">
                    <div className="col">
                        <div className="card">
                            <img src={Img1} class="card-img-top" alt="..." />
                            <div className="text py-4 d-block">
                                <div className="meta">
                                    <div><Link>Sept 10, 2018</Link></div>
                                    <div><Link>Admin</Link></div>
                                    <div><Link className="meta-chat"><span className="icon-chat"></span> 3</Link></div>
                                </div>
                                <h3 className="heading-title mt-2"><Link>The Delicious Pizza</Link></h3>
                                <p>A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                            </div>
                        </div>
                    </div>
                    <div className="col">
                        <div className="card">
                            <img src={Img2} className="card-img-top" alt="..." />
                            <div className="text py-4 d-block">
                                <div className="meta">
                                    <div><Link>Sept 10, 2018</Link></div>
                                    <div><Link>Admin</Link></div>
                                    <div><Link className="meta-chat"><span className="icon-chat"></span> 3</Link></div>
                                </div>
                                <h3 className="heading-title mt-2"><Link>The Delicious Pizza</Link></h3>
                                <p>A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                            </div>
                        </div>
                    </div>
                    <div className="col">
                        <div className="card">
                            <img src={Img3} className="card-img-top" alt="..." />
                            <div className="text py-4 d-block">
                                <div className="meta">
                                    <div><Link>Sept 10, 2018</Link></div>
                                    <div><Link>Admin</Link></div>
                                    <div><Link className="meta-chat"><span className="icon-chat"></span> 3</Link></div>
                                </div>
                                <h3 className="heading-title mt-2"><Link>The Delicious Pizza</Link></h3>
                                <p>A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Section>
    )
}


const Section = styled.div`
     .heading-section{
        padding: 7em 0;
     }
    .heading-text{
    line-height: 1.4;
    color: #fff;
    font-weight: 400;
    font-family: "Josefin Sans", Arial, sans-serif;
    font-size: 30px;
    font-weight: 400;
    text-transform: uppercase;
    
    @media (min-width: 768px){
        .heading-section{
    -webkit-box-flex: 0;
    -ms-flex: 0 0 58.33333%;
    flex: 0 0 58.33333%;
    max-width: 58.33333%;
     }
  }
}
.heading-section p{
    font-family: "Poppins", Arial, sans-serif;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    color: gray;
}
img {
    overflow: hidden;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    display: block;
    width: 100%;
    height: 270px;
    object-fit: cover;
}
.meta{
    display: flex;
    font-family: "Poppins", Arial, sans-serif;
    font-size: 15px;
    line-height: 1.8;
    font-weight: 300;
    a{
        color: gray;
    }
}
.card{
    /* width: 100%; */
    border: none;
    background: none;
    p{
        color: gray;
    }
}
.heading-title{
        line-height: 1.4;
        font-size: 18px;
        font-weight: 400;
        font-family: "Josefin Sans", Arial, sans-serif;
        text-transform: inherit;
        a{
            color: #fff;
        }
    }
a{
    text-decoration: none;
}
`
export default BlogCom
